.section_text {
	position: relative;

	display: grid;
	grid-template-columns: calc((100% - 1240px)/2) 3.3fr 6.6fr calc((100% - 1240px)/2);

	margin-bottom: rem(80);
	overflow: hidden;

	background-color: #F9F9FF;

	@include max {
		grid-template-columns: calc((100% - 1600px)/2) 3.3fr 6.6fr calc((100% - 1600px)/2);
	}

	@include hd {
		grid-template-columns: calc((100% - 2400px)/2) 3.3fr 6.6fr calc((100% - 2400px)/2);
	}

	@include w1200_w900 {
		grid-template-columns: calc((100% - 900px)/2) 3.3fr 6.6fr calc((100% - 900px)/2);
	}

	@include min1050 {
		grid-template-columns: calc((100% - 1050px)/2) 3.3fr 6.6fr calc((100% - 1050px)/2);
	}

	@include min {
		grid-template-columns: calc((100% - 900px)/2) 3.3fr 6.6fr calc((100% - 900px)/2);
	}


	@include min768 {
		width: 100%;
		padding: 0 rem(20) rem(58) rem(20);
		display: flex;
		flex-wrap: wrap;
		margin: rem(40) auto rem(90) auto;
		// padding-bottom: rem(58);
	}

	&-left {
		grid-column: 1 / span 2;
		height: rem(543);

		@include min768 {
			height: auto;
			order: 2;
			margin-top: rem(24);
			width: 100%;
			display: flex;
			justify-content: center;
		}

		.image {
			position: absolute;
			top: -27%;
			left: 0;
			width: rem(476);
			height: rem(680);
			background: url(../img/main_text-bg.png) no-repeat;
			// object-fit: cover;
			background-size: cover;

			@include min1050 {
				width: 100%;
				height: 100%;
				top: 7%;
				
				background: url(../img/main_text-bg_x2.png) no-repeat;
				background-size: 20%;
			}

			@include min {
				width: rem(249);
				height: rem(458);
				top: 7%;
				background: url(../img/main_text-bg_x2.png) no-repeat;
			}

			@include min768 {
				position: relative;
				top: auto;
				left: auto;
				width: rem(308);
				height: rem(308);
				background: url(../img/main_text-bg_x3.png) no-repeat;
			}
		}
	}

	&-right {
		grid-column-end: span 1;
		padding-left: rem(50);
		display: flex;
		flex-direction: column;
		justify-content: center;

		@include min {
			padding-left: rem(20);
		}

		@include min768 {
			padding-left: 0;
			order: 1;
		}

		.title {
			font-size: rem(38);
			color: #121212;
			font-weight: 600;
			margin-bottom: rem(26);

			@include min {
				font-size: rem(44);
			}

			@include min768 {
				font-size: rem(34);
			}
		}
	}
}